import { QueryKey, useMutation, useQuery } from "react-query";
import { Board } from "types/board";
import { useHttp } from "./http";
import {
  useAddConfig,
  useDeleteConfig,
  useReorderBoardConfig,
} from "./use-optimistic-options";

export const useBoards = (param?: Partial<Board>) => {
  const client = useHttp();

  return useQuery<Board[]>(["kanbans", param], () =>
    client("kanbans", { data: param })
  );
};

export const useAddBoard = (queryKey: QueryKey) => {
  const client = useHttp();
  return useMutation(
    (params: Partial<Board>) =>
      client(`kanbans`, {
        method: "POST",
        data: params,
      }),
    useAddConfig(queryKey)
  );
};

export const useDeleteBoard = (queryKey: QueryKey) => {
  const client = useHttp();

  return useMutation(
    ({ id }: { id: number }) =>
      client(`kanbans/${id}`, {
        method: "DELETE",
      }),
    useDeleteConfig(queryKey)
  );
};

export interface SortProps {
  type: "before" | "after";
  referenceId: number;
  fromId: number;
  fromBoardId?: number;
  toBoardId?: number;
}

export const useReorderBoard = (queryKey: QueryKey) => {
  const client = useHttp();

  return useMutation((params: SortProps) => {
    return client("kanbans/reorder", {
      data: params,
      method: "POST",
    });
  }, useReorderBoardConfig(queryKey));
};
