import { Input, Modal, Form, Button } from "antd";
import { useForm } from "antd/lib/form/Form";
import { TaskTypeSelect } from "components/task-type-select";
import { UserSelect } from "components/user-select";
import React, { useEffect } from "react";
import { useDeleteTask, useEditTask } from "utils/task";
import { useTasksModal, useTasksQueryKey } from "./util";

const layout = {
  labelCol: { span: 8 },
  wrapperCol: { span: 16 },
};
export const TaskModal = () => {
  const [form] = useForm();
  const { editingTask, editingTaskId, close } = useTasksModal();
  const { mutateAsync: editTask, isLoading: editLoading } = useEditTask(
    useTasksQueryKey()
  );
  const { mutate: deleteTask } = useDeleteTask(useTasksQueryKey());

  const startDelete = () => {
    close();
    Modal.confirm({
      okText: "confirm",
      cancelText: "cancel",
      title: "are you sure to delete this task?",
      onOk() {
        return deleteTask({ id: Number(editingTaskId) });
      },
    });
  };

  const onCancel = () => {
    close();
    form.resetFields();
  };

  const onOk = async () => {
    await editTask({ ...editingTask, ...form.getFieldsValue() });
    close();
  };

  useEffect(() => {
    form.setFieldsValue(editingTask);
  }, [form, editingTask]);

  return (
    <Modal
      forceRender={true}
      okText={"confirm"}
      cancelText={"cancel"}
      confirmLoading={editLoading}
      title={"edit task"}
      visible={!!editingTaskId}
      onCancel={onCancel}
      onOk={onOk}
    >
      <Form {...layout} initialValues={editingTask} form={form}>
        <Form.Item
          label={"task name"}
          name={"name"}
          rules={[{ required: true, message: "please" }]}
        >
          <Input />
        </Form.Item>
        <Form.Item label={"processor name"} name={"processorId"}>
          <UserSelect defaultOptionName={"processor name"} />
        </Form.Item>
        <Form.Item label={"type"} name={"typeId"}>
          <TaskTypeSelect defaultOptionName={"type"} />
        </Form.Item>
        <Button
          onClick={startDelete}
          style={{ fontSize: "14px" }}
          size={"small"}
        >
          delete
        </Button>
      </Form>
    </Modal>
  );
};
