import { useDocumentTitle } from "utils";
import { useBoards, useReorderBoard } from "utils/board";
import {
  useBoardSearchParams,
  useBoardsQueryKey,
  useProjectInUrl,
  useTasksQueryKey,
  useTasksSearchParams,
} from "./util";
import { BoardColumn } from "./board-column";
import styled from "@emotion/styled";
import { SearchPanel } from "./search-panel";
import { ScreenContainer } from "components/lib";
import { useReorderTask, useTasks } from "utils/task";
import { Spin } from "antd";
import { CreateBoard } from "./create-board";
import { TaskModal } from "./task-modal";
import { DragDropContext, DropResult } from "react-beautiful-dnd";
import { Drop, DropChild, Drag } from "components/drag-and-drop";
import { useCallback } from "react";

export const BoardScreen = () => {
  useDocumentTitle("Board List");
  const { data: currentProject } = useProjectInUrl();
  const { data: boards, isLoading: boardIsLoading } = useBoards(
    useBoardSearchParams()
  );
  const { isLoading: taskIsLoading } = useTasks(useTasksSearchParams());
  const isLoading = taskIsLoading || boardIsLoading;
  const onDragEnd = useDragEnd();
  return (
    <DragDropContext onDragEnd={onDragEnd}>
      <ScreenContainer>
        <h1>{currentProject?.name} board</h1>
        <SearchPanel />
        {isLoading ? (
          <Spin size={"large"}></Spin>
        ) : (
          <ColumnsContainer>
            <Drop
              type={"COLUMN"}
              direction={"horizontal"}
              droppableId={"board"}
            >
              <DropChild style={{ display: "flex" }}>
                {boards?.map((board, index) => (
                  <Drag
                    key={board.id}
                    draggableId={"board" + board.id}
                    index={index}
                  >
                    <BoardColumn board={board} key={board.id} />
                  </Drag>
                ))}
              </DropChild>
            </Drop>
            <CreateBoard />
          </ColumnsContainer>
        )}
        <TaskModal />
      </ScreenContainer>
    </DragDropContext>
  );
};

export const useDragEnd = () => {
  const { data: boards } = useBoards(useBoardSearchParams());
  const { mutate: reorderBoard } = useReorderBoard(useBoardsQueryKey());
  const { data: allTasks = [] } = useTasks(useTasksSearchParams());
  const { mutate: reorderTask } = useReorderTask(useTasksQueryKey());

  return useCallback(
    ({ source, destination, type }: DropResult) => {
      if (!destination) {
        return;
      }
      if (type === "COLUMN") {
        const fromId = boards?.[source.index].id;
        const toId = boards?.[destination.index].id;
        if (!fromId || !toId || fromId === toId) {
          return;
        }
        const type = destination.index > source.index ? "after" : "before";
        reorderBoard({ fromId, referenceId: toId, type });
      }

      if (type === "ROW") {
        const fromBoardId = +source.droppableId;
        const toBoardId = +destination.droppableId;
        if (toBoardId === fromBoardId) {
          return;
        }
        const fromTask = allTasks?.filter(
          (task) => task.kanbanId === fromBoardId
        )[source.index];
        const toTask = allTasks?.filter((task) => task.kanbanId === toBoardId)[
          destination.index
        ];
        if (fromTask?.id === toTask?.id) {
          return;
        }
        const type =
          fromBoardId === toBoardId && destination.index > source.index
            ? "after"
            : "before";
        reorderTask({
          fromId: fromTask?.id,
          referenceId: toTask?.id,
          type,
          fromBoardId,
          toBoardId,
        });
      }
    },
    [boards, reorderBoard, allTasks, reorderTask]
  );
};

export const ColumnsContainer = styled("div")`
  display: flex;
  overflow-x: scroll;
  margin-right: 2rem;
  flex: 1;
`;
